@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #b8c6db;
  background-image: linear-gradient(315deg, #b8c6db 0%, #f5f7fa 100%);
  font-family: 'Poppins', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  overflow: hidden;
  padding-bottom: 1rem;
  margin: 0;
}

.quiz-container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px 2px rgba(100, 100, 100, 0.1);
  width: 600px;
  overflow: hidden;
  text-align: center;
}

.quiz-header,
.score {
  text-align: center;
  font-weight: bold;
}

.score {
  padding: 1rem;
}

h1,
h2 {
  padding: 1rem;
  text-align: center;
  margin: 0;
}

button {
  background-color: #8e44ad;
  color: #fff;
  border: none;
  display: block;
  width: 100%;
  cursor: pointer;
  font-size: 1.1rem;
  font-family: inherit;
  padding: 1.3rem;
}

button:hover {
  background-color: #732d91;
}

select {
  border: none;
  padding: 1rem;
  display: block;
  width: 100%;
  font-size: 1.1rem;
  font-family: inherit;
}

select:focus {
  outline: none;
}

button:focus {
  outline: none;
  background-color: #5e3370;
}

